<template>
  <div class="timeLineBox">
    <div class="time">
      {{ time | time }}
    </div>
    <div class="textBox">
      <a :href="url">
        <slot></slot>
      </a>
    </div>
  </div>
</template>
<script>
// @ is an alias to /src
export default {
  name: "timeLine",
  props: ["time", "url"],
  data() {
    return {};
  },
  filters: {
    time(val) {
      let t = new Date((val + "000") / 1);
      function f(val) {
        return val < 10 ? "0" + val : val;
      }
      return f(t.getMonth() +1) +"月"+f(t.getDate())+"日" +"  " +f(t.getHours()) +":" +f(t.getMinutes())
      
    }
  },

  components: {}
};
</script>
<style lang="less">
.timeLineBox {
  padding-left: 20px;
  position: relative;
  .time {
    color: #999;
    font-size: 14px;
    position: relative;
    &::before {
      content: "";
      display: initial-block;
      position: absolute;
      left: -15px;
      top: 7px;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background: rgb(4, 247, 17);
      box-shadow: 0px 0px 0px 2px rgb(149, 229, 243);
    }
  }
  .textBox {
    background-color: #ddd;
    line-height: 40px;
    padding-left: 10px;
    box-sizing: border-box;
    border-radius: 5px;
    margin: 10px 0;
    a {
      text-decoration: none;
      color: #333;
      font-weight: 600;
      font-size: 20px;
      display: block;
    }
    &::before {
      content: "";
      display: block;
      position: absolute;
      width: 1px;
      top: 20px;
      left: 8px;
      height: 60px;
      background: #eee;
    }
  }
}
</style>
