<template>
  <div class="newsBox">
    <h2>实时播报</h2>
    <mytimeline
      v-for="(item, index) in list"
      :key="index"
      :url="item.eventUrl"
      :time="item.eventTime"
      >{{ item.eventDescription }}</mytimeline
    >
    <el-button @click="more">{{ str }}</el-button>
  </div>
</template>
<script>
// @ is an alias to /src
import { getNewsData } from "@/api";
import mytimeline from "./timeLine";
export default {
  name: "news",
  data() {
    return {
      list: [],
      allList: [],
      str: "显示更多"
    };
  },
  created() {
    getNewsData().then(data => {
      this.list = data.items.slice(0, 5);
      this.allList = data.items;
    });
  },
  methods: {
    more() {
      if (this.list.length > 10) {
        this.list = this.allList.slice(0, 5);
        this.str = '显示更多'
      } else {
        this.list = this.allList.slice(0);
        this.str = '点击收回'
      }
    }
  },
  components: {
    mytimeline
  }
};
</script>
<style lang="less">
.newsBox {
  margin: 20px auto;
  width: 700px;
  padding: 20px 0;
  box-sizing: border-box;
}
</style>
